@import '../../common/styles/variables';

.about-container {
  margin: 102px 0 200px 0;
  @include grid-with-two-columns;
}

.about-text {
  @include big-fonts;
  color: $turquoiseWithOpacity;
  display: flex;
  align-items: center;
  padding: 0 75px;
}

.about-image-container {
  border: 10px solid $turquoiseWithOpacity;
  @include circle-radius;
  @include square-size;
  background-image: url("../../common/assets/icons/oocyte.svg");
  @include background-position;
  margin-left: 40px;
}

.embryos-problem {
  height: 900px;
  margin-bottom: 100px;

  &__header {
    text-align: center;
    @include big-fonts;
    color: $whiteColor;
  }
}

.technology-schema {
  width: 1238px;
  height: 691px;
  margin-bottom: 150px;
  background-image: url("../../common/assets/icons/technologySchema.svg");
  @include background-position;
}

.description {
  padding: 0 310px;
  text-align: center;
  @include big-fonts;
  color: $whiteColor;
}

.contact-us {
  margin-top: 150px;

  &__container {
    @include grid-with-two-columns;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.google-link {
  text-decoration: none;
  white-space: nowrap;
  display: block;
  width: 200px;
  @include flex-align-center;
  font-size: 84px;
  color: $brownTextColor;
}
